import React from "react";
import Chart from "react-apexcharts";
import type { ApexOptions } from "apexcharts";  // Importing type
import { priceData } from "../assets/priceData";

const Prices = () => {
  

  const series = [
    {
      name: "Price",
      data: priceData.map((d) => ({
        x: new Date(d.time_open * 1000),
        y: [
          parseFloat(d.open),
          parseFloat(d.high),
          parseFloat(d.low),
          parseFloat(d.close)
        ]
      }))
    }
  ];

  const options: ApexOptions = {
    chart: {
      type: "candlestick", //  type string  "candlestick"  .
      height: 350,
      toolbar: {
        show: false, //   (,   )
      },
    },
    title: {
      text: undefined
    },
    legend: {
      show: false, //  
    },
    xaxis: {
      type: "datetime",
    },
    yaxis: {
      tooltip: {
        enabled: true,
      },
    },
    grid: {
      show: false, // (Grid Lines) 
    },
    plotOptions: {
      candlestick: {
        colors: {
          upward: "#1E88E5",  //   (  )
          downward: "#FB8C00" //   (  )
        }
      }
    }
  };

  return <Chart options={options} series={series} type="candlestick" height={350} width={500} />;
};

export default Prices;