import React from "react";
import Chart from "react-apexcharts";
import { chartData } from "../assets/chartData";

const Charts = () => {
  const options: ApexCharts.ApexOptions = {
    chart: {
      type: "candlestick", //  type string  "candlestick"  .
      height: 350,
      toolbar: {
        show: false, //   (,   )
      },
    },
    title: {
      text: undefined
    },
    legend: {
      show: false, //  
    },
    xaxis: {
      type: "datetime",
    },
    yaxis: {
      tooltip: {
        enabled: true,
      },
    },
    grid: {
      show: false, // (Grid Lines) 
    },
    plotOptions: {
      candlestick: {
        colors: {
          upward: "#1E88E5",  //   (  )
          downward: "#FB8C00" //   (  )
        }
      }
    }
  };

  return <Chart options={options} series={chartData} type="candlestick" height={350} width={500}/>;
};

export default Charts;